@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300;400;500&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  box-sizing: border-box;
  font-size: 18px;
  font-style: normal;
  font-weight: normal;
  text-align: left;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
:before,
:after {
  box-sizing: inherit;
}

img {
  display: block;
}

caption,
th,
td {
  text-align: inherit;
}

html,
body,
th,
td {
  padding: 0;
}

html,
body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ol,
ul,
dl,
dd,
figure,
blockquote,
hr,
input,
button,
select,
textarea {
  margin: 0;
}
tt,
code,
kbd,
samp,
pre {
  font-family: code, monospace;
}

h1,
h2,
h3,
h4,
h5,
h6,
th,
em,
cite,
strong,
small,
input,
button,
select,
text-area {
  font-style: inherit;
  font-size: inherit;
  font-weight: inherit;
}

ol,
ul {
  list-style: none;
  padding-left: 0;
}

table {
  border-collapse: collapse;
}

u,
s,
strike,
del,
ins {
  text-decoration: inherit;
}

input,
button,
select,
textarea {
  font-family: inherit;
  line-height: inherit;
}

[type='date'],
[type='email'],
[type='number'],
[type='password'],
[type='search'],
[type='tel'],
[type='url'],
[type='text'],
[type='button'],
[type='submit'],
[type='reset'],
button,
textarea {
  background-color: transparent;
  -webkit-appearance: none;
  appearance: none;
  border-width: 0;
  border-radius: 0;
  padding: 0;
}

[type='button'],
[type='submit'],
[type='reset'],
button {
  cursor: pointer;
  text-align: inherit;
}
html, body, .app, #root, .container {
  height: 100%;
}


body {
  font-family: 'Work Sans', sans-serif;
  --primary-color: #FFA800;
  --secondary-color: #ffffff;
  --color-white: #F6F6F6;
  --color-black: #000000;
  background-color: var(--color-white);
}

form {
  display: -webkit-flex;
  display: flex;
  padding-left: 8px;
  padding-bottom: 10px;
  border-bottom: 1px solid #3D3D3D;
}

label, input {
  width: 100%;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.tasks-list-wrapper {
  -webkit-flex: 1 1;
          flex: 1 1
}

@media only screen and (min-width: 600px) {
  body {
    background-color: #E5E5E5;
  }
}











.submit-button {
  padding: 0;
  background: transparent;
  border: none;
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: center;
          align-self: center;
}

.submit-button:hover {
  cursor: pointer;
}

.submit-button:focus {
  outline: none;
}
.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow: scroll;
  padding: 0 28px 14px 28px;
  background-color: var(--color-white);
  padding-bottom: 70px;
}

@media only screen and (min-width: 600px) {
  .container {
    padding: 24px 28px;
    width: 60%;
    margin: 0 auto;
    max-width: 1220px
  }
}




.doneTasks-wrapper {
  margin-top: 34px; 
}

.doneTasks-content {
  font-size: 24px;
  line-height: 28px;
  color: var(--primary-color)
}

.header {
  padding: 16px 23px;
  color: var(--color-white);
  background-color: var(--primary-color);
}

.header h1 {
  font-weight: 500;
  font-size: 24px;
}
.icon-button {
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: center;
          align-self: center;
  padding: 0;
  background: transparent;
  border: none;
}

.icon-button:hover {
  cursor: pointer;
}

.icon-button:focus {
  outline: none;
}

.item {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px 8px;
  margin-bottom: 12px;
  background-color: var(--color-white);
}



.item[data-is-mobile="false"]:hover .trash {
  display: block;
}

.item[data-is-mobile="false"]:hover {
  background-color: rgba(255, 168, 0, 0.2);
}

.trash {
  display: none
}

.circle-btn-input-wrapper { 
  display: -webkit-flex; 
  display: flex; 
  -webkit-flex-grow: 1; 
          flex-grow: 1; 
  -webkit-align-items: center; 
          align-items: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
 }

 .circle-btn-wrapper {
   margin-right: 16px;
 }

.cross-task {
  text-decoration: line-through;
  text-decoration-thickness: 1px;
}

.input-edit-task {
  padding: 0;
  margin: 0;
  font: inherit;
  line-height: 28px;
  font-size: 24px;
  outline: none;
  border: none;
  color: var(--color-black);
  background-color: transparent;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.input-edit-task::-webkit-input-placeholder { 
  line-height: 28px;
  color: var(--primary-color);
}

.input-edit-task::placeholder { 
  line-height: 28px;
  color: var(--primary-color);
}





.swipingTrashWrapper {
  display: -webkit-flex;
  display: flex;
  justify-self: center;
  background-color: #EA3124;
  padding-left: 24px;
  color: #F6F6F6;
  height: 100%;
}
.list-wrapper {
  margin-top: 26px
}

.list-wrapper[data-is-done-tasks="true"] {
  margin-top: 12px
 }

.input-new-task {
  margin-left: 16px;
  line-height: 28px;
  font-size: 24px;
  outline: none;
  border: none;
  color: var(--color-black);
  background-color: transparent;
}

.input-new-task::-webkit-input-placeholder { 
  line-height: 28px;
  color: var(--primary-color);
}

.input-new-task::placeholder { 
  line-height: 28px;
  color: var(--primary-color);
}



